.videoFooter{
    position: relative;
    margin-left: 20px;
    bottom : 100px;

}

.videoFooter_text{
    position :absolute;
    bottom:0;
    color: white;
    display: flex;
    margin-bottom: 20px;

}
.videoFooter_text >h3{
    margin-left: 10px;
    padding-bottom: 20px;
}
.videoFooter_text >h3 >button{
    color: white;
    font-weight: 900;
    text-transform: inherit;
}
.videoFooter_actionsLeft > .MuiSvgIcon-root{
    padding: 0 10px;
}
/* this MuiSvgIcon-root target icons on actionleft class  */
.videoFooter_actionsRight{
    display:flex;
}
.videoFooter_stat {
display: flex;
align-items: center;
margin-right: 10px;
}

.videoFooter_stat >p{
    margin-left: 3px;
}
.videoFooter_text > .ticker{
    height: fit-content;
    margin-left: 30px;
    margin-bottom: 20px;
    width: 60%;
}
.videoFooter_ticker h1 {
    padding-top: 7px;
    font-size: 12px;
    color: white;
}
.videoFooter_icon{
    position: absolute;
    left: 5px;
    color: white;
}
.videoFooter_actions{
    display:flex;
    color: white;
    position: absolute;
    width: 95%;
    justify-content: space-between;
}